//
export default{
	number2(num){//2
	    if (num<10) {
	        return '0'+num;
	    }else{
	        return num;
	    }
	},
	formate(data,type=" "){// 
		if (typeof data !="object") {
			data=new Date(data);
		}
		var time=this.number2(data.getHours())+":"+this.number2(data.getMinutes());
		var date=data.getFullYear()+"-"+this.number2((data.getMonth()+1))+"-"+this.number2(data.getDate());
		// if (new Date().getFullYear()===data.getFullYear()) {//
		// 	date=this.number2((data.getMonth()+1))+""+this.number2(data.getDate())+"";
		// }
	    if (type==='time') {
	        return time;
	    }else if (type=='date'){
	        return date;
	    }else{
	    	return date+" "+time;
	    }
	}
}



